$bavAppName: bav;

.suggestion {
  .iban-part.part-different {
    color: red;
    font-weight: bold;
  }
}
.dialog-app-icon {
  position: absolute;
  left: 0;
  top: 0;
  padding-block: 4px 0;
  padding-inline: 12px 0;
}
.about {
  max-width: 60ex;
  margin: 1ex;
  &.checksum {
    font-style: italic;
  }
  &.disclaimer {
    font-weight: bold;
    font-style: italic;
  }
}
